.planet-button {
  pointer-events: auto;
  cursor: pointer;
  $border-radius: 0.8em;
  border-radius: $border-radius;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  box-sizing: border-box;

  > .scene {
    background: rgba(91, 72, 72, 0.42);
    aspect-ratio: 1/1;
    max-height: 3em;
  }

  > span {
    padding: 0.4em 0.8em;
  }

  &.active {
    $margin-right: 1.9em;
    margin-right: $margin-right;
    //transform: scale(1.1);
    transition: all 200ms;
    position: relative;
    border: 0.3em solid red;

