.waiting {
  font-size: 1em;
  display: none;

  &.active {
    display: block;
  }

  > .circle {
    @keyframes rotation {
      100% {
        transform: rotate(360deg);
      }
    }

    background: rgba(173, 255, 47, 0.7);
    aspect-ratio: 1/1;
    width: 2em;
    border: 0.3em #000000 dashed;
    border-radius: 50%;
    animation: rotation 1000ms infinite linear;
    box-sizing: border-box;
  }

  .hourglass {
    font-size: 10em;
    position: absolute;
    width: 100%;
    height: 100%;
    background: rgba(232, 232, 232, 0);
    display: flex;
    align-items: center;
    justify-content: center;
    pointer-events: none;

    > svg {
      @keyframes loading-rotation {
        0% {
          transform: rotate(-180deg);
        }
        30% {
          transform: rotate(-180deg);
        }
        75% {
          transform: rotate(0deg);
        }
        100% {
          transform: rotate(0deg);
        }
      }

      fill: #2830c9;
      animation: loading-rotation 2.1s infinite linear;
    }

  }
}



