:root {
  --animation-modal-start: 800ms
}


.modal-start {
  width: 100%;
  height: 100%;
  position: absolute;
  background: rgba(147, 147, 147, 0.79);
  padding: 1.1em;
  box-sizing: border-box;

  & {
    transition: all var(--animation-modal-start);
    &.exit {
      opacity: 1;
    }
    &.exit-active {
      opacity: 0;
    }
    &.exit-done {
      opacity: 0;
    }
  }

  > div {
    width: 100%;
    height: 100%;
    border: 0.2em solid rgba(255, 255, 255, 0.62);
    border-radius: 4em;
    display: flex;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;
    position: relative;

    > .loading-bar {
      width: 80%;
      height: 0.4em;
      border-radius: 0.2em;
      bottom: 1em;
      background: white;
      position: absolute;
      transition: all var(--animation-modal-start);

      @keyframes anim {
        20% {
          width: 0%;
        }
        45% {
          width: 15%;
        }
        60% {
          width: 30%;
        }
        65% {
          width: 40%
        }
        70% {
          width: 40%;
        }
        90% {
          width: 56%;
        }
        100% {
          width: 60%;
        }
      }

      > .progress {
        background: #a40000;
        height: 100%;
        animation: anim 7s forwards;
        width: 0%;
      }
    }

    > button {
      font-size: 1.4em;
      background: none;
      border: none;
      border-radius: 0.2em;
      padding: 0.2em 0.5em;
      transition: all var(--animation-modal-start);

      &:hover {
        transition: all 700ms;
        transform: scale(1.3);
        background: white;
      }

      &:active {
        transition: all 0ms;
        transform: scale(1);
        background: #6b6969;
      }
    }
  }
}