.store-panels-planets {
  row-gap: 0.7em;
  font-size: 1em;
  padding-inline: 0.6em;
  display: flex;
  flex-direction: column;
  position: relative;
  align-items: flex-end;
  height: 35em;


  > .search-list {
    //background: greenyellow;
    //max-height: 20em;
    align-items: flex-end;

    > .search {
      border-radius: 0.4em;
      padding: 0.5em;
      border: 0;
      outline: none;
      text-align: right;
      font-size: inherit;
      pointer-events: auto;
    }

    > .list {
      pointer-events: auto;
      padding: 0.8em;
      row-gap: 0.9em;
      display: flex;
      flex-direction: column;
      list-style-type: none;
      align-items: flex-end;
      overflow-y: scroll;

      > .item {
        display: flex;
        column-gap: 0.2em;
        background: rgba(255, 0, 0, 0.32);

        > img {
          height: 1.3em;
          border-radius: 30%;
          background: rgba(225, 225, 225, 0.39);

          &:hover {
            background: rgba(213, 213, 213, 0.72);
          }
        }

        > .right-side {
          display: flex;
          flex-direction: column;
          row-gap: 0.5em;
          background: #0a4b78;

          > .planet-button {

          }

          > .satellites {
            font-size: 0.8em;
            max-height: 5em;
            row-gap: 0.5em;
            display: flex;
            flex-direction: column;
            align-items: flex-end;
            overflow-y: scroll;
          }
        }
      }
    }
  }
}