@import "planet/planets";
@import "info/info";
@import "actions/actions";

:root {
    --store-panels-padding: 0.5em;
    --store-panels-action-height: 3.2em;
}

.panels {
    width: 100%;
    height: 100%;
    row-gap: var(--store-panels-padding);
    padding: var(--store-panels-padding);;
    position: absolute;
    top: 0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    box-sizing: border-box;
    pointer-events: none;

    > .panels-top {
        height: 90%;
        overflow: hidden;
        column-gap: var(--store-panels-padding);
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
    }

    > .panels-top-left {
        width: 100%;
        row-gap: var(--store-panels-padding);
        display: flex;
        flex-direction: column;
    }
}






