.messages-block {
  min-height: 8em;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-sizing: border-box;

  > .messages-list {
    row-gap: 1em;
    max-height: 40em;
    width: 100%;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    overflow-y: scroll;
  }

  > .clear_logs {
    align-self: flex-end;
    height: 2em;;
  }

  .lack_of {
    font-size: 1.1em;
    padding: 0.5em;
    border-radius: 0.4em;
    background: rgb(255, 255, 255);
  }
}